<template>
  <div class="container">
    <div class="title"><h1>Todo App</h1></div>
    <div class="actions">
      <form @submit="addTask">
        <input type="text" class="input" placeholder="Add Task" v-model="task" required>
        <button type="submit">Add</button>
      </form>
    </div>
    <div class="tasks">
      <div class="task-items" v-for="todo in todos" :key="todo.id">
        <p :class="todo.status == true ? 'isDone':'text'">{{ todo.todo }}</p>
        <button class="done-btn"  @click="markAsDone(todo.id)">Done</button>
        <button class="remove-btn"   @click="removeTask(todo.id)">Remove</button>
      </div>
    </div>
  </div>
</template>

<script>
import {ref,onMounted} from 'vue'
import {db} from './firebase'
import { collection, query, onSnapshot,addDoc, orderBy, serverTimestamp, doc, deleteDoc, updateDoc } from "firebase/firestore";
export default {
  setup() {
    const task = ref('')
    const todos = ref([])

    const addTask = async (e)=>{
      e.preventDefault();
        // const newTask = {
        //   id:uuidv4(),
        //   details:this.task,
        //   status:false
        // }
        // this.todos.unshift(newTask);
        // this.task=''
        try{
            await addDoc(collection(db, "todos"), {
            todo: task.value,
            status: false,
            date: serverTimestamp()
          });
          task.value=''
        }catch(e){
          console.log(e);
        }
    }

    const removeTask = (id)=>{
      const fsquery = doc(db, 'todos', id);
        deleteDoc(fsquery)
        .then(() => {
            console.log('Document successfully deleted.');
          })
          .catch((error) => {
            console.error('Error deleting document:', error);
          });
    }
    const getRealTimeData = ()=>{
      const q = query(collection(db, "todos"), orderBy('date','desc'));
        onSnapshot(q, (querySnapshot) => {
          const newTask = [];
          querySnapshot.forEach((doc) => {
                const todo = {
                id:doc.id,
                todo:doc.data().todo,
                status:doc.data().status
              }
              newTask.push(todo);
          });
          todos.value=newTask;
        });
    }
    const markAsDone=(id)=>{
        const fsquery = doc(db, 'todos', id);
        const newData = {
          status:true
        }
        updateDoc(fsquery, newData)
        .then(() => {
          console.log('Document successfully updated.');
        })
        .catch((error) => {
          console.error('Error updating document:', error);
        });
      }
      onMounted(()=>{
        getRealTimeData();
      })

      return{
        task,todos, addTask,removeTask,markAsDone,getRealTimeData
      }
  }
}
</script>

